* {
  margin: 0;
  padding: 0;
}

#root {
    box-sizing: border-box;
    height: 93vh;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    outline: none;
    border-radius: 0;
}

.loading {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.header .row {
    display: flex;
    justify-content: space-between;
}
.dash {
    position: relative;
    width: 100%;
    height: 100%;
}

#dash-addBtn {
    position: fixed;
    bottom: 40px;
    right: 40px;
    padding: 0;
}

#dash-addBtn:hover,
#dash-addBtn:active {
    padding: 0 16px;
}
#dash-addBtn:hover::after,
#dash-addBtn:active::after {
    content:"Add Goal";
    white-space:nowrap;
}
.App {
    height: 100%;
    width: 100%;
}
